<template>
  <el-select v-model="value" placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
</template>

<script>
  export default {
    data() {
      return {
        options: [{
          value: '0',
          label: '一般'
        }, {
          value: '1',
          label: '紧急'
        }, {
          value: '2',
          label: '重要'
        }],
        value: '0'
      }
    }
  }
</script>


