import Vue from 'vue'
import Router from 'vue-router'
import Element from '@/components/element'
import TodoList from '@/components/todolist'
import About from '@/components/about'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Element',
      component: Element
    }, {
      path: '/todo',
      name: 'TodoList',
      component: TodoList
    }, {
      path: '/about',
      name: 'About',
      component: About
    }
  ]
})



