<template>
  <div class="todolist" v-on:keyup.enter="onAdd">
    <el-form :inline="true" :model="formInline">
      <el-form-item label="内容：">
        <el-input v-model="formInline.content" placeholder="请输入待办事项内容"></el-input>
      </el-form-item>
      <el-form-item label="类型：">
        <type-selector></type-selector>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onAdd">新增</el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="20">
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>未完成</span>
          </div>
          <div v-for="(todoItem, index) in todoItems" v-bind:key="todoItem.id" v-if="!todoItem.isFinished" class="text item">
            <i class="el-icon-arrow-right"></i>
            {{ todoItem.label }}
            <el-button style="float: right;" type="danger" icon="el-icon-delete" v-on:click="remove(index)" circle></el-button>
            <el-button style="float: right; margin-right: 5px" type="success" icon="el-icon-check" v-on:click="toggleFinish(todoItem)" circle></el-button>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>已完成</span>
          </div>
          <div v-for="(finishedItem, index) in todoItems" v-bind:key="finishedItem.id" v-if="finishedItem.isFinished" class="finished text item">
            <i class="el-icon-arrow-right"></i>
            {{ finishedItem.label }}
            <el-button style="float: right;" type="danger" icon="el-icon-delete" v-on:click="remove(index)" circle></el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Store from "./store";
import TypeSelector from "./typeselector";

export default {
  name: "TodoList",
  components: {
    TypeSelector
  },
  data() {
    return {
      msg: "待办事项",
      todoItems: Store.fetch(),
      newItem: "",
      formInline: {
        content: "",
        type: "",
        date: new Date()
      }
    };
  },
  watch: {
    todoItems: {
      handler: function(items) {
        Store.save(items);
      },
      deep: true
    }
  },
  methods: {
    toggleFinish: function(item) {
      item.isFinished = !item.isFinished;
    },
    onAdd: function() {
      if (null == this.todoItems || undefined == this.todoItems) {
        this.todoItems = [];
      }

      const trimmedText = this.formInline.content.trim();
      if("" == trimmedText) {
        this.formInline.content = "";
        return;
      }

      this.todoItems.push({
        id: this.todoItems.length + 1,
        label: trimmedText,
        isFinished: false
      });
      this.formInline.content = "";
    },
    remove: function(index) {
      this.todoItems.splice(index, 1);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.todolist {
  /* max-width: 400px; */
  margin: 20 auto;
  /* color: #00c185; */
}

.finished {
  /* text-decoration: underline; */
  color: #00c185;
}

.text {
  font-size: 15px;
  line-height: 3;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  /* width: 420px; */
}
</style>



