<template>
  <div class="element">
    <div class="banner">
      <h1>网站快速成型工具</h1>
      <p>Element，一套为开发者、设计师和产品经理准备的基于 Vue 2.0 的桌面端组件库</p>
    </div>

    <el-row :gutter="20">
      <el-col :span="3" class="tip-text">一般按钮：</el-col>
      <el-col :span="9">
        <el-button>搜索</el-button>
        <el-button type="success">保存</el-button>
        <el-button type="info">消息</el-button>
        <el-button type="warning">收藏</el-button>
      </el-col>

      <el-col :span="3" class="tip-text">圆形按钮：</el-col>
      <el-col :span="9">
        <el-button icon="el-icon-search" circle></el-button>
        <el-button type="primary" icon="el-icon-edit" circle></el-button>
        <el-button type="success" icon="el-icon-check" circle></el-button>
        <el-button type="info" icon="el-icon-message" circle></el-button>
        <el-button type="warning" icon="el-icon-star-off" circle></el-button>
        <el-button type="danger" icon="el-icon-delete" circle></el-button>
      </el-col>
    </el-row>

    <el-row :gutter="20">
      <el-col :span="3" class="tip-text">按钮组：</el-col>
      <el-col :span="9">
        <el-button-group>
          <el-button type="primary" icon="el-icon-edit">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete">刪除</el-button>
        </el-button-group>
      </el-col>
    </el-row>

    <el-row :gutter="20">
      <el-col :span="3" class="tip-text">文本框：</el-col>
      <el-col :span="9">
        <el-input placeholder="请输入待办事项" prefix-icon="el-icon-edit" suffix-icon="el-icon-delete" v-model="newTodoItem" clearable></el-input>
      </el-col>

      <el-col :span="3" class="tip-text">日期选择框：</el-col>
      <el-col :span="9">
        <date-picker></date-picker>
      </el-col>

    </el-row>

    <el-row :gutter="20">
      <el-col :span="3" class="tip-text">单选框：</el-col>
      <el-col :span="9">
        <type-selector></type-selector>
      </el-col>
      <el-col :span="3" class="tip-text">多选框：</el-col>
      <el-col :span="9">
        <multi-type-selector></multi-type-selector>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import Store from "./store";
import TypeSelector from "./typeselector";
import MultiTypeSelector from "./multi-typeselector";
import DatePicker from "./datepicker";

export default {
  name: "Element",
  components: {
    TypeSelector,
    MultiTypeSelector,
    DatePicker
  },
  data() {
    return {
      newTodoItem: ""
    };
  },
  watch: {
    // todoItems: {
    //   handler: function (items) {
    //     Store.save(items);
    //   },
    //   deep: true
    // }
  },
  created: function() {},
  methods: {
    toggleFinish: function(item) {
      item.isFinished = !item.isFinished;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.banner {
  text-align: center;
}
.tip-text {
  padding: 12px 20px;
  line-height: 1;
}
.el-row {
  margin-bottom: 20px;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
</style>



